body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}
html,
button {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}
body {
  margin: 0;
  background-color: #242424;
  color: #d2d2d2;
}
ul,
ol {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
.contenedor {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.teclado {
  width: 213px;
  display: flex;
  flex-wrap: wrap;
}
.teclado button {
  border: none;
  background-color: #383838;
  color: #d2d2d2;
  cursor: pointer;
  width: 71px;
  height: 71px;
  position: relative;
}
.teclado button:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 6px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.7);
  opacity: 1;
}
.teclado button.big {
  width: 142px;
}
.teclado button:disabled {
  cursor: default;
  opacity: 0.5;
}
.teclado button:not(disabled):hover {
  background-color: #454545;
}
.mensaje {
  height: 50px;
}
.telefono {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.acciones {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.numero {
  background-color: #fff;
  color: #454545;
  padding: 5px 20px;
  width: 150px;
  border-radius: 30px;
  text-align: center;
  margin: 50px 0;
  height: 31px;
}
.off {
  visibility: hidden !important;
}
